/*globals $:false */
const Utils = [() => {
	const service = {};

	service.handleError = function (err) {
		console.error(err);
		if (err.status === 401) {
			alert('you need to login first, thanks.');
		}
	};

	service.scrollBottom = function (selector, avoid) {
		if (!avoid) {
			setTimeout(function () {
				$(selector).scrollTop($(selector).prop('scrollHeight'));
			});
		}
	};

	service.onScrollTop = function (px, cb) {
		$('#chatWindow').on('scroll', function() {
			if ($(this).scrollTop() < px) {
				cb();
			}
		});
	};

	service.beep = function () {
		const snd = new Audio("data:audio/wav;base64,");  
		snd.play();
	};

	return service;
}];

export default Utils;